<template>
  <div id="recent-articles-wrapper" class="py-5">
    <h2 class="mb-5 coloured-underline">Recent Articles</h2>
    <BContainer id="recent-articles-container">
      <ClientOnly>
        <BRow>
          <BCol v-for="article in data" :key="article._path" class="mb-3" cols="12" lg="4" sm="6">
            <ContentRenderer :value="article">
              <BCard class="article-summary-card" :title="article.title" tag="article" no-body>
                <img class="card-img" :src="article.image.url" :img-alt="article.title" />
                <BCardBody>
                  <BLink :to="article._path">
                    <h4 class="text-start card-title">
                      {{ article.title }}
                    </h4>
                  </BLink>
                  <BCardText>
                    <div v-html="article.summary"></div>
                  </BCardText>
                  <BButton :to="article._path" variant="success">Read More</BButton>
                </BCardBody>
              </BCard>
            </ContentRenderer>
          </BCol>
        </BRow>
      </ClientOnly>
    </BContainer>
  </div>
</template>

<script setup>
  const date = new Date();
  const { data, error } = await useAsyncData("blog", async () => {
    const content = await queryContent("blog")
      .where({
        date: { $exists: true, $lte: date },
        // draft: { $not: true },
        hidden: { $not: true }
      })
      .sort({ date: -1 })
      // .limit(3)
      .find();

    return content;
  });

  if (error.value) {
    console.error(error.value);
  }
</script>

